import {Component, QueryList, ViewChildren, Output, EventEmitter} from '@angular/core';
import {LottonumberService} from '../lottonumber.service';
import {BallComponent} from '../ball/ball.component';

@Component({
  selector: 'app-save-data',
  templateUrl: './save-data.component.html',
  styleUrls: ['./save-data.component.css']
})
export class SaveDataComponent {

  saveData: any = {
    "drwtNo6": 0,
    "drwtNo4": 0,
    "drwtNo5": 0,
    "drwtNo2": 0,
    "saveNo": 0,
    "drwtNo3": 0,
    "drwtNo1": 0
  };

  winData: any = {
    "totSellamnt": 0,
    "returnValue": "success",
    "drwNoDate": "",
    "firstWinamnt": 0,
    "drwtNo6": 0,
    "drwtNo4": 0,
    "firstPrzwnerCo": 0,
    "drwtNo5": 0,
    "bnusNo": 0,
    "drwtNo2": 0,
    "drwNo": 0,
    "drwtNo3": 0,
    "drwtNo1": 0
  };

  @ViewChildren(BallComponent) viewBallList: QueryList<BallComponent>;
  @Output() showHoldView = new EventEmitter<boolean>();
  @Output() showSelectView = new EventEmitter<boolean>();
  @Output() showLengthView = new EventEmitter<boolean>();

  userNumbers: Array<any> = [];

  constructor(private numberService: LottonumberService) {
    this.userNumbers = this.numberService.getUserNumbers();
    /*Promise.resolve().then(this.numberService.getUserNumbers).then(data =>{
       this.userNumbers = data;
       this.userNumbers.sort((o1, o2) => o2.saveNo - o1.saveNo);
    });*/
  }

  setWinData(winData) {
    this.winData = winData;
    this.checkWinData();
    this.setRanking();
  }

  recheckBallUpdate(e){
    if(this.winData.drwNo != 0){
      setTimeout(() => {
        this.checkWinData();
        this.setRanking();
      },500);
    }
  }

  checkWinData(){
    this.viewBallList.forEach( ball =>{
      ball.isWinNumber(false);
      if(this.checkWinSaveNumber(ball.ballNumber)){
        ball.animationStatus('spin')
        ball.isWinNumber(true);
      }
    });
  }

  resetSaveBall(){
    console.log(2);
  }

  checkWinSaveNumber(saveNumber) {
    return (this.winData.drwtNo1 == saveNumber ||
    this.winData.drwtNo2 == saveNumber ||
    this.winData.drwtNo3 == saveNumber ||
    this.winData.drwtNo4 == saveNumber ||
    this.winData.drwtNo5 == saveNumber ||
    this.winData.drwtNo6 == saveNumber
    );
  }

  checkBonusSaveNumber(saveNumber){
    return this.winData.bnusNo == saveNumber;
  }

  setRanking() {
    this.userNumbers.forEach((saveData) => {
      document.getElementById('result' + saveData.saveNo).innerText = this.checkRanking(saveData);
    });
  }

  checkRanking(saveData) {
    let isMatchNum = 0;
    if (this.checkWinSaveNumber(saveData.drwtNo1)) isMatchNum++;
    if (this.checkWinSaveNumber(saveData.drwtNo2)) isMatchNum++;
    if (this.checkWinSaveNumber(saveData.drwtNo3)) isMatchNum++;
    if (this.checkWinSaveNumber(saveData.drwtNo4)) isMatchNum++;
    if (this.checkWinSaveNumber(saveData.drwtNo5)) isMatchNum++;
    if (this.checkWinSaveNumber(saveData.drwtNo6)) isMatchNum++;

    switch (isMatchNum) {
      case 3:
        return '5';
      case 4:
        return '4';
      case 5:
        if (this.checkBonusSaveNumber(saveData.drwtNo1)
          || this.checkBonusSaveNumber(saveData.drwtNo2)
          || this.checkBonusSaveNumber(saveData.drwtNo3)
          || this.checkBonusSaveNumber(saveData.drwtNo4)
          || this.checkBonusSaveNumber(saveData.drwtNo5)
          || this.checkBonusSaveNumber(saveData.drwtNo6)){

          return '2';
        }
        return '3';
      case 6:
        return '1';
      default:
        return '꽝';
    }
  }

  holdNumber(){
    this.showHoldView.emit(true);
  }

  lengthNumber(){
    this.showLengthView.emit(true);
  }

  selectNumber(){
    this.showSelectView.emit(true);
  }
}



