import {Component, OnInit, ViewChild} from '@angular/core';
import {LottonumberService} from './lottonumber.service';
import {BallComponent} from './ball/ball.component';
import {SaveDataComponent} from './save-data/save-data.component';
// import * as $ from 'jquery';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  @ViewChild('saveData') saveData: SaveDataComponent;
  @ViewChild('ball1') ball1: BallComponent;
  @ViewChild('ball2') ball2: BallComponent;
  @ViewChild('ball3') ball3: BallComponent;
  @ViewChild('ball4') ball4: BallComponent;
  @ViewChild('ball5') ball5: BallComponent;
  @ViewChild('ball6') ball6: BallComponent;
  @ViewChild('bnusNo') bnusNo: BallComponent;
  isMain:boolean = true;
  showHoldView:boolean = false;
  closeHoldView:boolean = false;
  closeSelectView:boolean = false;
  closeLengthView:boolean = false;
  showSelectView:boolean = false;
  showLengthView:boolean = false;
  winDataList: any;
  winData: any = {
    "totSellamnt": 0,
    "returnValue": "success",
    "drwNoDate": "",
    "firstWinamnt": 0,
    "drwtNo6": 0,
    "drwtNo4": 0,
    "firstPrzwnerCo": 0,
    "drwtNo5": 0,
    "bnusNo": 0,
    "drwtNo2": 0,
    "drwNo": 0,
    "drwtNo3": 0,
    "drwtNo1": 0
  };

  updateShowHoldView(e){
    this.showHoldView = !this.showHoldView;
    this.showSelectView = false;
    this.showLengthView = false;
    this.setMainStatus();
  }

  updateShowSelectView(e){
    this.showSelectView = !this.showSelectView;
    this.showHoldView = false;
    this.showLengthView = false;
    this.setMainStatus();
  }

  updateShowLengthView(e){
    this.showLengthView = !this.showLengthView;
    this.showHoldView = false;
    this.showSelectView = false;
    this.setMainStatus();
  }

  setMainStatus(){
    if(this.showSelectView || this.showHoldView || this.showLengthView){
      this.isMain = false;
    }else{
      this.isMain = true;
    }

  }

  constructor(private lottoService: LottonumberService) {
  }

  getWinNumber() {
    this.lottoService.getWinNumber().subscribe(
      data => {
        this.winDataList = data;
        this.winDataList.sort((o1, o2) => o2.drwNo - o1.drwNo);
        this.winData = this.winDataList[0];
        this.saveData.setWinData(this.winData);
        this.callTrigger();
      },
      err => console.log(err),
      () => console.log('done loading winnumber')
    );
  }

  ngOnInit() {
    this.getWinNumber();
  }

  getWinData(drwNo) {
    this.showLengthView = false;
    this.showHoldView = false;
    this.showSelectView = false;
    this.setMainStatus();
    setTimeout(() => {
      this.winDataList.forEach( winData => {
        if(winData.drwNo == drwNo){
          this.winData = winData;
          this.saveData.setWinData(this.winData);
          this.callTrigger();
          return;
        }
      })
    }, 200);
  }

  callTrigger(){
    this.ball1.animationStatus('spin');
    this.ball2.animationStatus('spin');
    this.ball3.animationStatus('spin');
    this.ball4.animationStatus('spin');
    this.ball5.animationStatus('spin');
    this.ball6.animationStatus('spin');
    this.bnusNo.animationStatus('spin');
  }

  sendWinNumberForKakao(){
    console.log('kakao')
  }

  qrCameraCall(){
    console.log('qrcode')
  }
}



