import {Component, OnInit, Input, Output, EventEmitter} from "@angular/core";
import {trigger, style, transition, animate, keyframes, state} from "@angular/animations";
// import * as $ from 'jquery';

@Component({
  selector: 'app-ball',
  templateUrl: './ball.component.html',
  styleUrls: ['./ball.component.css'],
  animations: [
    trigger(
      'rotation', [
        state('stop, void', style({transform: 'rotate(0deg)'})),
        state('spin', style({transform: 'rotate(360deg)'})),
        transition(
          'stop => spin',
          [
            animate(
              1000,
              keyframes([
                style({transform: 'rotate(0deg)'}),
                style({transform: 'rotate(360deg)'})
              ])
            )
          ]
        )
      ]
    )
  ]
})
export class BallComponent implements OnInit{
  _id: string;
  _isWinBall: boolean = false;
  _animationStatus:string = 'stop';
  className: string;
  isMatch: boolean;
  private _ballNumber: number;
  @Output() recheckBall = new EventEmitter<string>();

  updateBall(){
    this.recheckBall.emit(this._id);
  }

  constructor() {
  }

  ngOnInit(){
  }

  @Input()
  set isWinBall(isWinBall:boolean){
    this._isWinBall = isWinBall;
  }

  get isWinBall():boolean{
    return this._isWinBall;
  }

  @Input()
  set ballNumber(ballNumber: number) {
    this._ballNumber = ballNumber;
    if (this._ballNumber < 10) {
      this.className = 'red';
    } else if (this._ballNumber < 20) {
      this.className = 'paran';
    } else if (this._ballNumber < 30) {
      this.className = 'purple';
    } else if (this._ballNumber < 40) {
      this.className = 'wien';
    } else {
      this.className = 'yellow';
    }

    if(this._ballNumber == 0){
      this.className = 'none';
    }
  }

  animationStatus(status){
    this._animationStatus = status;
  }

  get ballNumber(): number {
    return this._ballNumber;
  }

  @Input()
  set id(id){
    this._id=id;
    this.updateBall();
  }

  get id():string{
    return this._id;
  }

  getCSSClasses() {
    return {
      'ball': true,
      'red': this.className === 'red',
      'paran': this.className === 'paran',
      'purple': this.className === 'purple',
      'wien': this.className === 'wien',
      'yellow': this.className === 'yellow',
      'none' : this.className === 'none',
      'noMatch': this.isWinBall ? false : !this.isMatch
    }
  }

  isWinNumber(isTrue) {
    this.isMatch = isTrue;
  }

  animation(e){
    this._animationStatus = 'stop';
  }

}



