import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Subject} from "rxjs";
declare var Promise;
declare var IndexedDB;

const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
}


@Injectable()
export class LottonumberService {

  userNumbers:Array<any> = [];

  private componentMethodCall = new Subject<any>();

  constructor(private http: HttpClient) {

  }

  getWinNumber() {
    return this.http.get('https://parkminkyu.github.io/bok/lottoHistory.json');
  }

  saveUserNumber(numbers) {
    let saveData = {
      "drwtNo6": 0,
      "drwtNo4": 0,
      "drwtNo5": 0,
      "drwtNo2": 0,
      "saveNo": 0,
      "drwtNo3": 0,
      "drwtNo1": 0
    };

    saveData.drwtNo1 = numbers[0];
    saveData.drwtNo2 = numbers[1];
    saveData.drwtNo3 = numbers[2];
    saveData.drwtNo4 = numbers[3];
    saveData.drwtNo5 = numbers[4];
    saveData.drwtNo6 = numbers[5];

    Promise.resolve(saveData).then(this.insertUserNumberToIndexedDB).then(data => {
      this.userNumbers.push(data);
      this.userNumbers.sort((o1, o2) => o2.saveNo - o1.saveNo);
    })
  }

  insertUserNumberToIndexedDB(saveData){
    return new Promise(function(resolve, reject){
      IndexedDB.checkDB();
      IndexedDB.dataBaseName = 'MyLotto';
      IndexedDB.schemaName = 'number';
      IndexedDB.createSchema('saveNo');

      IndexedDB.selectMaxValue('keyIndex', function (data) {
        if (data == null)
          saveData.saveNo = 1;
        else
          saveData.saveNo = (data.saveNo + 1)

        IndexedDB.insert(saveData, function (data) {
          if (data == 1) {
            resolve(saveData);
          }
        });
      });
    });
  }

  getUserNumbers() {
    Promise.resolve().then(this.setUserNumbers).then(data => {
      data.sort((o1, o2) => o2.saveNo - o1.saveNo);
      data.forEach(d =>{
        this.userNumbers.push(d);
      })
    });
    return this.userNumbers;
  }

  setUserNumbers(){
    return new Promise(function(resolve, reject){
      IndexedDB.checkDB();
      IndexedDB.dataBaseName = 'MyLotto';
      IndexedDB.schemaName = 'number';
      IndexedDB.createSchema('saveNo');

      IndexedDB.selectAll(function (data) {
        resolve(data);
      });
    });
  }
}



