import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { BallComponent } from './ball/ball.component';
import { NumberPipe } from './number.pipe';
import { HttpClientModule, HttpClientJsonpModule } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { LottonumberService } from './lottonumber.service';
import { SaveDataComponent } from './save-data/save-data.component';
import { HoldNumberComponent } from './hold-number/hold-number.component';
import { RandomNumberComponent } from './random-number/random-number.component';
import { AlertViewComponent } from './alert-view/alert-view.component';
import { SelectNumberComponent } from './select-number/select-number.component';
import { LengthNumberComponent } from './length-number/length-number.component';

@NgModule({
  declarations: [
    AppComponent,
    BallComponent,
    NumberPipe,
    SaveDataComponent,
    HoldNumberComponent,
    RandomNumberComponent,
    AlertViewComponent,
    SelectNumberComponent,
    LengthNumberComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    HttpClientJsonpModule
  ],
  providers: [
    LottonumberService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



