import {Component, OnInit, EventEmitter, Output, ViewChild} from '@angular/core';
import {BallComponent} from "../ball/ball.component";
import {AlertViewComponent} from "../alert-view/alert-view.component";
import {LottonumberService} from "../lottonumber.service";

@Component({
  selector: 'app-length-number',
  templateUrl: './length-number.component.html',
  styleUrls: ['../hold-number/hold-number.component.css']
})
export class LengthNumberComponent implements OnInit {

  @ViewChild('alertView') alertView: AlertViewComponent;
  @Output() closeLengthView = new EventEmitter<boolean>();
  length1: number = 0;
  length2: number = 0;
  length3: number = 0;
  length4: number = 0;
  length5: number = 0;

  saveNumbers = [1, 1, 1, 1, 1, 1];

  closeLengthNumber() {
    this.closeLengthView.emit(true);
  }

  setNumber(type, number) {
    switch (type) {
      case 1:
        this.length1 = number;
        break;
      case 2:
        this.length2 = number;
        break;
      case 3:
        this.length3 = number;
        break;
      case 4:
        this.length4 = number;
        break;
      case 5:
        this.length5 = number;
        break;
    }

    if (this.length1 + this.length2 + this.length3 + this.length4 + this.length5 > 6) {
      this.alertView.isShow = true;
      this.alertView.message = '지정 범위 합이 6이 넘습니다';
      this.setNumber(type, 0)
      return;
    }
  }

  createChoiceNumber() {
    this.alertView.isShow = false;
    if (this.length1 + this.length2 + this.length3 + this.length4 + this.length5 > 6) {
      this.alertView.isShow = true;
      this.alertView.message = '지정 범위 합이 6이 넘습니다';
      return;
    }

    let choiceLength = [this.length1, this.length2, this.length3, this.length4, this.length5];
    let createNumbers = [];

    for (let i = 0; i < choiceLength.length; i++) {
      for (let k = 0; k < choiceLength[i]; k++) {
        let rndNum = Math.floor(Math.random() * (9 + (i > 0 ? 1 : 0)) + (i > 0 ? 0 : 1)) + (i * 10);
        if (rndNum > 45) {
          rndNum = Math.floor(Math.random() * 5 + 1) + (40);
        }
        createNumbers.push(rndNum);
      }
    }

    for (let i = 0; i < createNumbers.length; i++) {
      if (createNumbers.filter(v => v == createNumbers[i]).length > 1) {
        this.createChoiceNumber();
        return;
      }
    }

    while (createNumbers.length < 6) {
      for (let i = 0; i < (6 - createNumbers.length); i++) {
        let rndNum = Math.floor(Math.random() * 45 + 1);

        if (createNumbers.filter(v => v == rndNum).length > 0) {
          i--;
        } else {
          createNumbers.push(rndNum);
        }
      }
    }

    this.saveNumbers = createNumbers.sort((o1, o2) => o1 - o2);
  }

  saveNumber() {
    this.saveNumbers.sort((o1, o2) => o1 - o2);
    for (let i = 0; i < this.saveNumbers.length; i++) {
      for (let k = (i + 1); k < this.saveNumbers.length; k++) {
        if (this.saveNumbers[i] == this.saveNumbers[k]) {
          this.alertView.isShow = true;
          this.alertView.message = '중복된 번호가 있습니다.'
          return;
        }
      }
    }

    this.lottoNumberService.saveUserNumber(this.saveNumbers)
  }

  constructor(private lottoNumberService: LottonumberService) {
  }

  ngOnInit() {
  }

}



